/* Global styles */
:root {
  --bg: #0f172a;
  --text: #e2e8f0;
  --muted: #94a3b8;
  --accent: #38bdf8;
}

html, body {
  height: 100%;
  width: 100%;
}

app-root {
  display: flex;
  flex-direction: column;
  flex: 1;
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
  background: var(--bg);
  color: var(--text);
  display: flex;
  flex-direction: column;
  width: 100%;
}

a {
  color: var(--accent);
  text-decoration: none;
}

.container {
  width: 100%;
  //max-width: 1024px;
  margin: 0 auto;
  padding: 1rem;
  box-sizing: border-box;

  &.main {
    max-width: none;
    padding: 0;
    flex: 1;
    display: flex;
    flex-direction: column;
  }
}
